$mint_medium: #c0ffe6; // medium mint
$color_2: #eee;
$dark_gray: #3d3d3d;
$font_1: Averta-Regular, Avenir, Helvetica Neue, Calibri, Helvetica, Roboto,
	sans-serif;

.NavContainer {
	display: flex;
	justify-content: flex-start;
	position: sticky;
	top: 0;
	background-color: #fff;
	z-index: 10;
	min-width: 400px;

	.logo {
		height: 75px;
		width: 75px;
		margin: 35px 0 0 35px;
		box-shadow: 0 2px 6px 0 #d3d3d3;
		border-radius: 50%;
	}

	.logo:hover {
		box-shadow: 0 0 8px 12px #d9fff0;
	}

	ul {
		display: flex;
		margin-top: 3%;
		margin-left: 60%;
		font-family: $font_1;
		font-size: 16px;
		font-weight: 500;
		width: 100%;

		li {
			display: flex;
			list-style: none;
			color: #000;
			width: 150px;
			align-items: center;

			// nav numbers
			div {
				color: $dark_gray;
				margin-right: 5px;
			}
		}
		li:hover {
			color: $mint_medium;
			cursor: pointer;
		}
	}

	@media only screen and (max-width: 1440px) {
		ul {
			margin-left: 50%;
		}
	}

	@media only screen and (max-width: 1280px) {
		ul {
			margin-left: 40%;
		}
	}

	@media only screen and (max-width: 900px) {
		.logo {
			display: none;
		}
		ul {
			display: none;
		}
	}
}

.resume-button {
	background-color: $mint_medium;
	color: #3d3d3d;
	border: 1px solid #d3d3d3;
	box-shadow: 0 2px 6px 0 #d3d3d3;
	border-radius: 20px;
	padding: 8px 20px;
	height: 100%;
	width: 100%;
	font-family: $font_1;
	font-size: 16px;
	font-weight: 500;
}

.resume-button:hover {
	background-color: $mint_medium;
	color: #d3d3d3;
	transition: all 0.5s;
	cursor: pointer;
}

.first {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 1s;
}

.second {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 1.5s;
}

.third {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 2s;
}

.fourth {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 2.5s;
}

.fifth {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 3s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.icon {
	display: none;
	color: $mint_medium;
	margin-left: 85%;
	margin-top: 2.5%;
}

@media screen and (max-width: 700px) {
	.NavContainer ul {
		display: none;
	}
	.NavContainer a.icon {
		display: block;
		float: right;
	}
}

@media screen and (max-width: 700px) {
	.NavContainer.responsive {
		position: relative;
	}
	.NavContainer.responsive a.icon {
		position: absolute;
		right: 0;
		top: 0;
	}
	.NavContainer.responsive a {
		float: none;
		display: block;
		text-align: left;
	}
}

.react-slidedown.my-dropdown-slidedown {
	transition-duration: 2s;
}
